.header {
  justify-content: space-between;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  margin-top: 10px;
}

.logotype {
  width: 40px;
  height: 40px;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.list__item {
  margin-left: 20px;
  position: relative;
}

.list__item:first-child {
  margin-left: 0;
}

.link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.link:hover {
  color: #00acdc;
}

.navigation {
  height: 60px;
  margin-top: 0;
  margin-bottom: 0;
}

.is-active {
  color: #00acdc;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.catalog {
  grid-template-columns: 200px;
  gap: 46px 48px;
  padding-top: 50px;
  padding-left: 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: 200px 200px;
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: 200px 200px 200px;
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: 200px 200px 200px 200px;
  }
}

@media (width >= 1200px) {
  .catalog {
    grid-template-columns: 200px 200px 200px 200px;
    padding-left: 128px;
  }
}

.container {
  box-sizing: border-box;
  border-width: 1px;
  border-color: #f3f3f3;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 200px;
  margin-block: 0;
  transition: transform .3s ease-in-out;
}

.container__picture {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
  margin: 33px 20px 40px;
}

.container__text {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin-top: 0;
  margin-bottom: 4px;
  margin-left: 17px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.container__code {
  text-align: left;
  color: #616070;
  margin: 0 17px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.container__stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.container__stars-review {
  color: #060b35;
  text-align: center;
  margin-right: 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.container__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin: 0 17px 16px;
  display: flex;
}

.container__price-title {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.container__price-sum {
  text-align: right;
  color: #000;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.container__button {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 17px;
  margin-left: 17px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.container__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid;
}

.container:hover {
  transform: scale(1.2);
}

.container:hover .container__text {
  color: #34568b;
}

.stars {
  margin-left: 16px;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
  background-repeat: no-repeat;
}

body {
  color: #060b35;
